import { randomInteger } from './random';
import { ElevenLabsService } from './services/audio/ElevenLabsService';
import { env } from './config/env';

export interface Speech {
    speak(phrase: string, context: any): Promise<void>;
    speakRandomPhrase(phrases: string[], context: any): Promise<void>;
}

export class BrowserSpeech implements Speech {
    async speak(phrase: string, context: any) {
        const utterance = new SpeechSynthesisUtterance(phrase);
        window.speechSynthesis.speak(utterance);
    }

    async speakRandomPhrase(phrases: string[], context: any) {
        const idx = randomInteger(0, phrases.length);
        await this.speak(phrases[idx], context);
    }
}

export class MockSpeech implements Speech {
    async speak(phrase: string, context: any) {
        // No-op for testing
    }

    async speakRandomPhrase(phrases: string[], context: any) {
        const idx = randomInteger(0, phrases.length);
        await this.speak(phrases[idx], context);
    }
}

export class ElevenLabsSpeech implements Speech {
    private service: ElevenLabsService;

    constructor() {
        this.service = new ElevenLabsService(
            env.ELEVENLABS_API_KEY,
            env.ELEVENLABS_VOICE_ID
        );
    }

    async speak(phrase: string, context: any) {
        await this.service.speak(phrase);
    }

    async speakRandomPhrase(phrases: string[], context: any) {
        const idx = randomInteger(0, phrases.length);
        await this.speak(phrases[idx], context);
    }
}

export default function getSpeech(): Speech {
    if (process.env.NODE_ENV === 'test') {
        return new MockSpeech();
    }
    return new ElevenLabsSpeech();
}
