import { deserializeReport } from '../serialization';
import * as React from 'react';
import { Report } from '../models';
import ReportCard from './ReportCard';
import { Box, Button, Typography, Paper, TextField } from '@mui/material';


interface ReportViewerProps {
    onBack(): void;
}


interface ReportViewerState {
    reportSource: string;
    report?: Report;
}


export default class ReportViewer extends React.Component<ReportViewerProps, ReportViewerState> {
    state: ReportViewerState = {
        reportSource: '',
    };

    render() {
        return (
            <Box sx={{ 
                maxWidth: '1200px', 
                mx: 'auto', 
                p: 3
            }}>
                <Paper 
                    elevation={3}
                    sx={{
                        background: 'rgba(0, 0, 0, 0.2)',
                        backdropFilter: 'blur(10px)',
                        border: '1px solid rgba(255, 255, 255, 0.1)',
                        p: 4,
                        borderRadius: 2
                    }}
                >
                    <Typography variant="h4" component="h2" sx={{ 
                        mb: 4,
                        background: 'linear-gradient(45deg, #fff, #ccc)',
                        WebkitBackgroundClip: 'text',
                        WebkitTextFillColor: 'transparent',
                        fontWeight: 600
                    }}>
                        View the Report of a Previous Punishment
                    </Typography>

                    <Button 
                        variant="outlined"
                        onClick={this.props.onBack}
                        sx={{ 
                            mb: 4,
                            borderColor: '#c41e3a',
                            color: '#ffffff',
                            '&:hover': {
                                borderColor: '#ff5252',
                                backgroundColor: 'rgba(196, 30, 58, 0.1)',
                            }
                        }}
                    >
                        Back
                    </Button>

                    <Box component="form" sx={{ mb: 4 }}>
                        <TextField
                            fullWidth
                            multiline
                            rows={12}
                            label="Punishment report"
                            value={this.state.reportSource}
                            onChange={this.onChange}
                            variant="outlined"
                            helperText="If you instructed someone to take a punishment and received a punishment report from them, enter the report here to view its contents."
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '& fieldset': {
                                        borderColor: 'rgba(255,255,255,0.2)',
                                    },
                                    '&:hover fieldset': {
                                        borderColor: 'rgba(255,255,255,0.3)',
                                    },
                                    '&.Mui-focused fieldset': {
                                        borderColor: 'rgba(255,255,255,0.5)',
                                    },
                                },
                                '& .MuiInputLabel-root': {
                                    color: 'rgba(255,255,255,0.7)',
                                },
                                '& .MuiInputBase-input': {
                                    color: 'white',
                                },
                                '& .MuiFormHelperText-root': {
                                    color: 'rgba(255,255,255,0.5)',
                                }
                            }}
                        />
                    </Box>

                    {this.state.report && (
                        <Box sx={{ mt: 4 }}>
                            <ReportCard report={this.state.report} />
                        </Box>
                    )}
                </Paper>
            </Box>
        );
    }

    onChange = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let reportSource = event.target.value;
        let report: Report | undefined = undefined;

        try {
            report = deserializeReport(reportSource);
        } catch (e) {
            // Wasn't a valid report. Won't show anything.
        }

        this.setState({ report, reportSource });
    }
}
