import React from 'react';
import { Box, Button, Container, Paper, Typography } from '@mui/material';
import { motion } from 'framer-motion';
import PunishmentStateMachine from '../state';

interface WelcomeScreenProps {
    fsm: PunishmentStateMachine;
    onCustom(): void;
    onPreset(): void;
    onReport(): void;
}

const MotionButton = motion(Button);
const MotionPaper = motion(Paper);

const WelcomeScreen: React.FC<WelcomeScreenProps> = ({ fsm, onCustom, onPreset, onReport }) => {
    return (
        <Container maxWidth="md" sx={{ py: 4 }}>
            <MotionPaper
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6 }}
                elevation={3}
                sx={{
                    p: 4,
                    background: 'linear-gradient(45deg, #1a1a1a 0%, #2c2c2c 100%)',
                    border: '1px solid #c41e3a',
                }}
            >
                <Typography 
                    variant="h1" 
                    component="h1" 
                    gutterBottom 
                    sx={{
                        color: '#c41e3a',
                        textAlign: 'center',
                        textTransform: 'uppercase',
                        fontSize: { xs: '2.5rem', md: '3.5rem' },
                        mb: 3,
                    }}
                >
                    Corner Time
                </Typography>

                <Typography 
                    variant="h6" 
                    sx={{ 
                        textAlign: 'center', 
                        mb: 4,
                        color: 'text.secondary',
                        fontStyle: 'italic'
                    }}
                >
                    Submit to your punishment and face the consequences
                </Typography>

                <Box sx={{ mb: 4 }}>
                    <MotionButton
                        variant="contained"
                        fullWidth
                        size="large"
                        onClick={() => fsm.getReady()}
                        whileHover={{ scale: 1.02 }}
                        whileTap={{ scale: 0.98 }}
                        sx={{
                            mb: 2,
                            fontSize: '1.2rem',
                            py: 2,
                            background: 'linear-gradient(45deg, #c41e3a 30%, #ff5252 90%)',
                        }}
                    >
                        Begin Your Punishment (10-15 Minutes)
                    </MotionButton>
                </Box>

                <Box sx={{ 
                    display: 'flex', 
                    flexDirection: { xs: 'column', sm: 'row' },
                    gap: 2,
                    justifyContent: 'center',
                    mb: 4
                }}>
                    <Button 
                        variant="outlined" 
                        onClick={onCustom}
                        sx={{ 
                            borderColor: '#c41e3a',
                            color: '#ffffff',
                            '&:hover': {
                                borderColor: '#ff5252',
                                backgroundColor: 'rgba(196, 30, 58, 0.1)',
                            }
                        }}
                    >
                        Mistress: Create a Custom Punishment
                    </Button>
                    <Button
                        variant="outlined" 
                        onClick={onPreset}
                        sx={{ 
                            borderColor: '#c41e3a',
                            color: '#ffffff',
                            '&:hover': {
                                borderColor: '#ff5252',
                                backgroundColor: 'rgba(196, 30, 58, 0.1)',
                            }
                        }}
                    >
                        Sub: Receive Your Punishment
                    </Button>
                    <Button 
                        variant="outlined" 
                        onClick={onReport}
                        sx={{ 
                            borderColor: '#c41e3a',
                            color: '#ffffff',
                            '&:hover': {
                                borderColor: '#ff5252',
                                backgroundColor: 'rgba(196, 30, 58, 0.1)',
                            }
                        }}
                    >
                        Mistress: Review Your Sub's Report
                    </Button>
                </Box>

                <Typography 
                    variant="body2" 
                    sx={{ 
                        textAlign: 'center', 
                        mb: 2,
                        color: 'text.secondary',
                        opacity: 0.8
                    }}
                >
                    Voice instructions will guide you. Position your webcam 2-3 meters from where you'll stand.
                </Typography>

                <Typography 
                    variant="body2" 
                    sx={{ 
                        textAlign: 'center',
                        color: 'text.secondary',
                        opacity: 0.8
                    }}
                >
                    Camera only detects movement - No images or videos will be sent over the internet or stored.
                </Typography>
            </MotionPaper>
        </Container>
    );
};

export default WelcomeScreen;
