import * as React from 'react';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import PunishmentStateMachine from '../state';
import { create } from 'diffyjs';
import getSettings from '../settings';
import WelcomeScreen from './WelcomeScreen';
import PunishmentSetup from './PunishmentSetup';
import PunishmentLoader from './PunishmentLoader';
import ReportCard from './ReportCard';
import ReportViewer from './ReportViewer';
import { darkTheme } from '../theme/darkTheme';
import { Box } from '@mui/material';

import 'bootstrap/dist/css/bootstrap.css';
import { formatDuration } from '../time';

const MOTION_MAX = 255;
type SetupScreen = 'default' | 'custom' | 'report' | 'preset';

interface AppState {
    setupScreen: SetupScreen;
}

class App extends React.Component<{}, AppState> {
    fsm = new PunishmentStateMachine();
    diffyInstance: any = null;

    state: AppState = {
        setupScreen: 'default',
    };

    componentDidMount() {
        this.fsm.addListener(this.handleFsmUpdate);
        
        // Only initialize motion detection if not in test environment
        if (process.env.NODE_ENV !== 'test') {
            this.initializeMotionDetection();
        }
    }

    componentWillUnmount() {
        this.fsm.removeListener(this.handleFsmUpdate);
        if (this.diffyInstance && typeof this.diffyInstance.dispose === 'function') {
            this.diffyInstance.dispose();
        }
    }

    initializeMotionDetection = () => {
        try {
            const settings = getSettings();
            this.diffyInstance = create({
                resolution: settings.diffy.resolution,
                sensitivity: settings.diffy.sensitivity,
                threshold: settings.diffy.threshold,
                sourceDimensions: settings.diffy.sourceDimensions,
                debug: settings.diffy.debug,
                containerClassName: 'motion-detection',
                captureIntervalTime: 150,
                onFrame: (matrix: number[][]) => {
                    if (!matrix || matrix.length === 0) return;
                    
                    // Calculate motion magnitude from the matrix
                    const minValue = Math.min(...matrix.map(row => Math.min(...row)));
                    const magnitude = (MOTION_MAX - minValue) / MOTION_MAX;
                    
                    this.fsm.handleMotionUpdate(magnitude);
                }
            });

            // Add minimal styles
            const style = document.createElement('style');
            style.textContent = `
                .motion-detection {
                    position: fixed;
                    top: -9999px;
                    left: -9999px;
                    width: 1px;
                    height: 1px;
                    opacity: 0;
                    pointer-events: none;
                    z-index: -1;
                }
                .motion-detection canvas,
                .motion-detection video {
                    width: 1px !important;
                    height: 1px !important;
                    opacity: 0 !important;
                }
            `;
            document.head.appendChild(style);

            console.log('Motion detection initialized with settings:', settings.diffy);
        } catch (error) {
            console.error('Failed to initialize motion detection:', error);
        }
    };

    render() {
        const fsm = this.fsm;

        return (
            <ThemeProvider theme={darkTheme}>
                <CssBaseline />
                <Box sx={{ 
                    minHeight: '100vh',
                    background: 'linear-gradient(to bottom, #121212 0%, #1a1a1a 100%)',
                    pt: 2
                }}>
                    {this.renderContent(fsm)}
                </Box>
            </ThemeProvider>
        );
    }

    renderContent(fsm: PunishmentStateMachine) {
        switch (fsm.state) {
            case 'waiting':
                switch (this.state.setupScreen) {
                    case 'custom':
                        return <PunishmentSetup fsm={fsm} onBack={this.returnToWelcomeScreen} />;
                    case 'preset':
                        return <PunishmentLoader fsm={fsm} onBack={this.returnToWelcomeScreen} />;
                    case 'report':
                        return <ReportViewer onBack={this.returnToWelcomeScreen} />;
                    default:
                        return (
                            <WelcomeScreen
                                fsm={fsm}
                                onCustom={this.setUpCustom}
                                onPreset={this.loadPreset}
                                onReport={this.viewReport}
                            />
                        );
                }

            case 'preparation':
                return (
                    <Box sx={{ 
                        display: 'flex', 
                        alignItems: 'center', 
                        justifyContent: 'center',
                        minHeight: '80vh'
                    }}>
                        <h1 className="display-2 text-center">
                            The punishment will start in {formatDuration(-fsm.currentTime)}.
                        </h1>
                    </Box>
                );

            case 'punishment':
            case 'cooldown':
                return (
                    <Box sx={{ 
                        display: 'flex', 
                        alignItems: 'center', 
                        justifyContent: 'center',
                        minHeight: '80vh'
                    }}>
                        <h1 className="display-1 text-center">
                            {formatDuration(fsm.timeLeft)}
                        </h1>
                    </Box>
                );

            case 'finished':
                return <ReportCard report={fsm.report()} showMessage={true} />;

            default:
                return null;
        }
    }

    setUpCustom = () => this.setState({ setupScreen: 'custom' });
    viewReport = () => this.setState({ setupScreen: 'report' });
    loadPreset = () => this.setState({ setupScreen: 'preset' });
    returnToWelcomeScreen = () => this.setState({ setupScreen: 'default' });

    handleFsmUpdate = () => {
        this.forceUpdate();
    }
}

export default App;
