import { deserializePreset } from '../serialization';
import PunishmentStateMachine from '../state';
import * as React from 'react';
import { Box, Button, Typography, Paper, TextField } from '@mui/material';


interface PunishmentLoaderProps {
    fsm: PunishmentStateMachine;
    onBack(): void;
}

interface PunishmentLoaderState {
    preset: string;
    isValid: boolean;
}


export default class PunishmentLoader extends React.Component<PunishmentLoaderProps, PunishmentLoaderState> {
    state = {
        preset: '',
        isValid: false,
    };

    render() {
        return (
            <Box sx={{ 
                maxWidth: '1200px', 
                mx: 'auto', 
                p: 3
            }}>
                <Paper 
                    elevation={3}
                    sx={{
                        background: 'rgba(0, 0, 0, 0.2)',
                        backdropFilter: 'blur(10px)',
                        border: '1px solid rgba(255, 255, 255, 0.1)',
                        p: 4,
                        borderRadius: 2
                    }}
                >
                    <Typography variant="h4" component="h2" sx={{ 
                        mb: 4,
                        background: 'linear-gradient(45deg, #fff, #ccc)',
                        WebkitBackgroundClip: 'text',
                        WebkitTextFillColor: 'transparent',
                        fontWeight: 600
                    }}>
                        Carry Out a Custom Punishment
                    </Typography>

                    <Box sx={{ display: 'flex', gap: 2, mb: 4 }}>
                        <Button 
                            variant="contained" 
                            onClick={this.start}
                            disabled={!this.state.isValid}
                            sx={{ 
                                flex: 1,
                                fontSize: '1.2rem',
                                py: 2,
                                background: this.state.isValid ? 
                                    'linear-gradient(45deg, #c41e3a 30%, #ff5252 90%)' : 
                                    'rgba(255,255,255,0.1)',
                            }}
                        >
                            Start the Custom Punishment Now
                        </Button>
                        <Button 
                            variant="outlined"
                            onClick={this.props.onBack}
                            sx={{ 
                                borderColor: '#c41e3a',
                                color: '#ffffff',
                                '&:hover': {
                                    borderColor: '#ff5252',
                                    backgroundColor: 'rgba(196, 30, 58, 0.1)',
                                }
                            }}
                        >
                            Back
                        </Button>
                    </Box>

                    <Box component="form" sx={{ mt: 2 }}>
                        <TextField
                            fullWidth
                            multiline
                            rows={24}
                            label="Custom punishment"
                            value={this.state.preset}
                            onChange={this.onChange}
                            variant="outlined"
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '& fieldset': {
                                        borderColor: 'rgba(255,255,255,0.2)',
                                    },
                                    '&:hover fieldset': {
                                        borderColor: 'rgba(255,255,255,0.3)',
                                    },
                                    '&.Mui-focused fieldset': {
                                        borderColor: 'rgba(255,255,255,0.5)',
                                    },
                                },
                                '& .MuiInputLabel-root': {
                                    color: 'rgba(255,255,255,0.7)',
                                },
                                '& .MuiInputBase-input': {
                                    color: 'white',
                                }
                            }}
                        />
                    </Box>
                </Paper>
            </Box>
        );
    }

    start = (event: React.MouseEvent<HTMLButtonElement>) => {
        const fsm = this.props.fsm;

        fsm.loadPreset(this.loadPreset());
        fsm.getReady();
    }

    loadPreset() {
        try {
            return deserializePreset(this.state.preset);
        } catch (e) {
            return JSON.parse(this.state.preset);
        }
    }

    onChange = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        const preset = event.target.value;

        let isValid = false;
        try {
            deserializePreset(preset);
            isValid = true;
        } catch (e) {
            // noop;
        }

        this.setState({ preset, isValid });
    }
}
