interface VoiceSettings {
    stability: number;        // Range: 0-1
    similarity_boost: number; // Range: 0-1
    style: number;           // Range: 0-1
    use_speaker_boost: boolean;
}

export class ElevenLabsService {
  private apiKey: string;
  private voiceId: string;
  private currentAudio: HTMLAudioElement | null = null;
  private isPlaying: boolean = false;

  // Voice settings configuration
  private voiceSettings: VoiceSettings = {
    stability: 0.75,        // Range: 0-1. Higher value = more stable, lower = more variable
    similarity_boost: 1,    // Range: 0-1. Higher value = more similar to original voice
    style: 0.50,           // Range: 0-1. Higher value = more stylized
    use_speaker_boost: true // Enhances voice clarity and reduces noise
  };

  constructor(apiKey: string, voiceId: string) {
    console.log('ElevenLabs initialized with:', { apiKey, voiceId }); 
    console.log('API Key length:', apiKey?.length, 'Voice ID length:', voiceId?.length);
    console.log('First 10 chars of API key:', apiKey?.substring(0, 10));
    
    this.apiKey = apiKey;
    this.voiceId = voiceId;
  }

  // Method to update voice settings
  setVoiceSettings(settings: Partial<VoiceSettings>) {
    this.voiceSettings = { ...this.voiceSettings, ...settings };
  }

  async speak(text: string): Promise<void> {
    try {
      // Ensure any previous audio is fully stopped
      if (this.currentAudio) {
        this.currentAudio.pause();
        this.currentAudio.currentTime = 0;
        this.currentAudio.src = '';
        this.currentAudio = null;
      }

      // Wait for any previous playback to fully stop
      if (this.isPlaying) {
        await new Promise(resolve => setTimeout(resolve, 100));
      }

      const response = await fetch(`https://api.elevenlabs.io/v1/text-to-speech/${this.voiceId}`, {
        method: 'POST',
        headers: {
          'Accept': 'audio/mpeg',
          'Content-Type': 'application/json',
          'xi-api-key': this.apiKey,
        },
        body: JSON.stringify({
          text,
          model_id: 'eleven_monolingual_v1',
          voice_settings: this.voiceSettings
        })
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(`ElevenLabs API error: ${JSON.stringify(error)}`);
      }

      const audioBlob = await response.blob();
      const audio = new Audio(URL.createObjectURL(audioBlob));
      
      // Set up event handlers
      audio.addEventListener('play', () => {
        this.isPlaying = true;
      });
      
      audio.addEventListener('ended', () => {
        this.isPlaying = false;
        if (this.currentAudio === audio) {
          this.currentAudio = null;
        }
      });

      audio.addEventListener('pause', () => {
        this.isPlaying = false;
      });

      this.currentAudio = audio;
      await audio.play();

    } catch (error) {
      this.isPlaying = false;
      this.currentAudio = null;
      throw error;
    }
  }
}